import React, { useState, useEffect, useContext } from 'react'
import ourReducer from './logic/OurReducer'
import Paginators from './components/Paginators/Paginators'
import GlyphCard from './components/GlyphCard/GlyphCard'
import Inspector from './components/Inspector/Inspector'
import SearchInput from './components/SearchInput/SearchInput'
import ModeToggle from './components/ModeToggle/ModeToggle'
import GlyphNamesToggle from './components/GlyphCardControls/GlyphCardControls'
import { unicodeData } from './unicode/UnicodeData'
import StateContext from './StateContext'
import DispatchContext from './DispatchContext'
import FlashAlerts from './components/FlashAlerts/FlashAlerts'

// import { useParams } from 'react-router'

import { useImmerReducer } from 'use-immer'
import './App.css'

const Entities = require('html-entities').AllHtmlEntities
const initialResultsPerPage = 200
const useStateWithLocalStorage = (localStorageKey, defaultValue) => {
  console.log('localStorage.getItem(localStorageKey) ', localStorageKey, localStorage.getItem(localStorageKey))
  const [value, setValue] = useState(localStorage.getItem(localStorageKey) || defaultValue)
  localStorage.setItem(localStorageKey, value)
  return [value, setValue]
}
function App() {
  const [currentPage, setCurrentPage] = useState(0)
  const [resultsPerPage, setResultsPerPage] = useState(initialResultsPerPage)
  const [modeToggle, setModeToggle] = useStateWithLocalStorage('modeToggle', 'entities')
  const [glyphnameToggle, setGlyphnameToggle] = useStateWithLocalStorage('glyphnameToggle', 'shownames')
  const originalState = {
    query: '',
    isSplitSeries: true,
    results: [],
    flashMessages: [],
    inspectorOpen: false,
    inspectorIndex: null
  }
  const modeHandler = state => setModeToggle(state)
  const toggleHandler = state => {
    setGlyphnameToggle(state === 'shownames' ? 'hidenames' : 'shownames')
  }

  const [uiStorage, setUiStorage] = useState(
    JSON.parse(localStorage.getItem('uiStorage')) || {
      glyphs: true,
      htmlEntities: false
    }
  )

  useEffect(() => {
    localStorage.setItem('uiStorage', JSON.stringify(uiStorage))
  }, [uiStorage])
  const validResults = item => {
    // return false
    return item !== undefined && item.length
  }
  const [state, dispatch] = useImmerReducer(ourReducer, originalState)
  function rangeHandler(results) {
    const rangeStart = resultsPerPage * currentPage
    const rangeEnd = rangeStart + resultsPerPage
    return [...results.slice(rangeStart, rangeEnd)]
  }
  const hasQuery = state.query && state.query.length
  return (
    <StateContext.Provider value={state}>
      <DispatchContext.Provider value={dispatch}>
        <div className="App">
          <header>
            <h1>GlyphWorks</h1>
            <div className="inputContainer">
              <SearchInput isSplitSeries={state.isSplitSeries} placeholder="Input chracters or search names" onInputChange={setCurrentPage} />
            </div>
          </header>
          <ModeToggle mode={modeToggle} modeHandler={modeHandler} />
          <div className={`outputContainer${modeToggle === 'entities' ? ' showentities' : ''}`}>
            <div className="glyphcardContainer">
              <div className={`glyphcardWrapper${state.inspectorOpen ? ' inspectoropen' : ''}`}>
                <GlyphNamesToggle toggle={glyphnameToggle} toggleHandler={toggleHandler} />
                <div className="glyphPanelContainer">
                  <div className="glyphPanelUnit">
                    <ul className={`glyphcardList${glyphnameToggle ? ' shownames' : ''}`}>{hasQuery && validResults(state.results) ? rangeHandler(state.results).map((item, index) => <GlyphCard item={unicodeData[item]} key={index} inspectoropen={state.inspectorOpen} />) : null}</ul>
                  </div>
                  <div className="glyphPanelUnit">{state.inspectorOpen && state.inspectorIndex ? <Inspector index={state.inspectorIndex} /> : ''}</div>
                </div>
              </div>
            </div>
            {/* {hasQuery && state.results.length > resultsPerPage ? <Paginators currentPage={currentPage} decrement={() => setCurrentPage(Math.max(0, currentPage - 1))} increment={() => setCurrentPage(Math.floor(Math.min(state.results.length / resultsPerPage, currentPage + 1)))} numPages={Math.floor((state.results.length - 1) / resultsPerPage)} /> : false} */}
            <div className="entityContainer">
              <label htmlFor="entity-url">URL addresses/mailto: links</label>
              <textarea readOnly id="entity-url" value={encodeURI(state.query)}></textarea>
              <label htmlFor="entity-all">URL addresses/mailto: links</label>
              <textarea readOnly id="entity-all" value={Entities.encode(state.query)}></textarea>
            </div>
          </div>
          <footer>
            <a className="swdLink" href="https://sidewaysdesign.com">
              <span className="infinity">
                <svg xmlns="http://www.w3.org/2000/svg" width="52.158" height="18.402" viewBox="0 0 52.158 18.402">
                  <path className="infinity__mark" fill="#d84a0e" d="M51.679,6.41C50.546,2.677,47.39.517,43.14.282a22.465,22.465,0,0,0-14.713,4.19,62.958,62.958,0,0,0-4.912,4.851A16.187,16.187,0,0,1,13.272,13.5c-2.661.144-6.866-.205-7.262-3.581A4.84,4.84,0,0,1,7.41,5.8C9.942,3.65,15.075,4.137,18.063,4.92c.152.038.89.243,1.376.388a.557.557,0,0,0,.6-.205l.327-.433a.551.551,0,0,0-.023-.7,8.345,8.345,0,0,0-1.65-1.506C14.352-.441,5.38-1.444,1.745,3.566-1.175,7.6-.939,14.615,3.874,17.147a10.588,10.588,0,0,0,4.768,1.285,22.028,22.028,0,0,0,13.223-3.939,24.29,24.29,0,0,0,3.6-3,26.124,26.124,0,0,1,4.768-4.327A15.1,15.1,0,0,1,41.566,5.14c.829.221,2.829,1.163,3.277,2.973.487,1.939-1.08,4.076-2.722,4.988a8.738,8.738,0,0,1-4.912.7c-1.65-.091-3.262-.5-4.889-.753a1.05,1.05,0,0,0-.16,0,.431.431,0,0,0-.281.19c-.342.532-.738,1.027-.038,1.521a18.4,18.4,0,0,0,6.783,2.981c3.581.768,7.55.494,10.349-2.015A9.591,9.591,0,0,0,51.679,6.41Z" transform="translate(0.159 -0.047)" />
                </svg>
              </span>
              <span>{`Â©${new Date().getFullYear()} Sideways Design`}</span>
            </a>
          </footer>
          <FlashAlerts messages={state.flashMessages} />
        </div>
      </DispatchContext.Provider>
    </StateContext.Provider>
  )
}

export default App
